.card{display:block;width:200px;height:fit-content;padding:12px;border-radius:12px;background-color:#3a8ac0;font-size:14px;color:#fff;margin:0 24px 24px 0;box-shadow:3px 3px 12px 1px rgba(0,0,0,.25);text-decoration:none}.card:hover{transition:1s ease;transform:scale(1.05);cursor:pointer}.card .deleteCard{display:flex;justify-content:flex-end}.card .deleteCard .deleteBtn{margin-left:5px}.card .deleteCard .btn{cursor:pointer}.card .deleteCard .btn:hover{box-shadow:2px 1px 7px 1px #224c63}.card .mainInfo{display:flex;flex-direction:column;align-items:center}.card .mainInfo .title{font-size:24px;font-weight:500}.card .mainInfo .description{margin-top:4px;font-size:16px}.card .mainInfo .temperature{margin-top:12px;font-size:40px;font-weight:500}.card .mainInfo .temperature:after{content:"°"}.card .information{margin-top:12px}.card .information div{margin-top:4px}.cardList:hover{transition:none;transform:none;cursor:default}